<style scoped lang="stylus">
.nav-area
    // ,  position  :class  fixed
    top 0px
    right 56px
    bottom 0px

    .navigation
        display flex
        flex-flow column nowrap
        // 
        background-color black
        margin-top 56px

        .nav-btn
            width 70px
            height 35px
            padding 20px
            font-weight bolder
            color white
            cursor pointer
            position relative
            transition color .2s

            span
                position relative
                line-height 40px
                z-index 1

            .nav-btn-background
                z-index 0
                position absolute
                width 0px
                height 100%
                background-color #666666
                transition width .2s
                top 0px
                left 0px

            &:hover
                color white

                .nav-btn-background
                    width 100%
    .back-top
        margin-top 35px
        height 35px
        width 70px
        padding 20px
        cursor pointer
        background-color black

        img
            width 30px

.fixed
    position fixed
.absolute
    position absolute
</style>

<template lang="pug">
.nav-area(ref="navBar" :class="fixed ? 'fixed' : 'absolute'")
    .navigation
        navigation-button(v-for="item, index in menus"
                          :key="index"
                          :selected="index == selectButtonIndex"
                          @click="jumpTo(item.value)")
            div {{item.label}}
    navigation-button.back-top(@click="backTop")
        img(src="@/images/icon/upArrow.svg")
</template>

<script>
/**
 * 
 * 
 * 
 * @param menus 
 *  @property {string} label 
 *  @property {string} value  html  css 
 */
import NavigationButton from './NavigationButton'

export default {
    name: 'NavigationBar',
    props: {
        // 
        menus: {
            type: Array,
            required: true
        },
        // 
        select: {
            type: Number,
            default: 0
        }
    },
    computed: {
        // 
        selectButtonStyle() {
            return {
                backgroundColor: '#666666',
                color: 'white'
            }
        }
    },
    data: () => ({
        // 
        selectButtonIndex: 0,
        // 
        fixed: false,
        // 
        navBarOffsetTop: 0
    }),
    components: { NavigationButton },
    watch: {
        select(val) {
            if (val >= 0 && val < this.menus.length) {
                this.selectButtonIndex = val
            }
        }
    },
    methods: {
        /**
         * 
         * 
         * 
         * @param {number} buttonIndex 
         * @param {string} selector  css 
         */
        onNavigateButtonClick(buttonIndex, selector) {
            this.selectButtonIndex = buttonIndex
            this.jumpTo(selector)
        },
        /**
         * 
         * 
         * @param {string} selectore  css 
         */
        jumpTo(selector) {
            document.querySelector(selector).scrollIntoView({ behavior: 'smooth' })
        },
        /**
         * 
         * 
         */
        onScroll() {
            const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            // console.log(scrollTop, this.navBarOffsetTop)
            this.fixed = scrollTop >= this.navBarOffsetTop
        },
        backTop() {
            window.scroll({ top: 0, left: 0, behavior: 'smooth' })
        }
    },
    mounted() {
        this.navBarOffsetTop = this.getElementToPageTop(this.$refs.navBar)
        window.addEventListener('scroll', this.onScroll)
    },
    destroyed () {
        window.removeEventListener('scroll', this.onScroll)
    }
}
</script>
