<style scoped lang="stylus">
+mediaDesktop()
    .page-title
        margin-top 60px
        width 100%
        display flex
        flex-flow column nowrap
        
        // 
        .my-name
            padding 20px 0px
            font-size 54px
        // 
        .my-summary
            font-size 21px
        // 
        hr 
            margin 30px 20%
            opacity 0.2
+mediaMobile()
    .page-title
        margin-top 30px
        width 100%
        display flex
        flex-flow column nowrap
        
        // 
        .my-name
            padding 10px 0px
            font-size 40px
        // 
        .my-summary
            font-size 18px
        // 
        hr 
            margin 30px 10%
            opacity 0.2
        
</style>

<template lang="pug">
.page-title
    .my-name(:style="fontStyle") {{title}}
    .my-summary(:style="fontStyle") {{summary}}
    hr(:style="borderStyle")
</template>

<script>
/**
 * 
 * 
 * 
 * @param {string} title 
 * @param {string} summary 
 * @param {string} color 
 * @param {string} borderColor 
 */
export default {
    name: 'PageTitle',
    props: {
        title: {
            type: String,
            required: true
        },
        summary: {
            type: String
        },
        color: {
            type: String,
            default: ''
        },
        borderColor: {
            type: String,
            default: '#727878'
        }
    },
    data: () => ({ }),
    computed: {
        fontStyle() {
            return {
                color: this.color
            }
        },
        borderStyle() {
            return {
                borderTop: `${this.borderColor} solid 2px`
            }
        }
    },
    methods: { }
}
</script>
