<style lang="stylus" scoped>
+mediaDesktop()
    @import '../../styles/desktop/infoPage/MyDetail'
+mediaMobile()
    @import '../../styles/mobile/infoPage/MyDetail'
</style>

<template lang="pug">
.detail-container
    page-title(title="" summary="")
    .detail
        .my-introduce 
            h3 
            p 
        .my-avatar
            .avatar-bordar
                img(src="https://z3.ax1x.com/2021/05/01/gZ9BOf.jpg")
        .my-introduce
            h3 
            p (AI)
    hr
    .sub-title 
    //- page-title(title="?" summary="")
    .link-area
        link-item.link-item(v-for="item, index in linkList" :key="index" :detail="item")
    navigation-bar.nav-bar(:menus="menus" :select="selectedPageIndex")
</template>

<script>
import PageTitle from '../PageTitle'
import NavigationBar from '../NavigationBar'
import LinkItem from '../LinkItem'

export default {
    name: 'MyDetail',
    data: () => ({
        linkList: [
            { 
                key: 'github', 
                label: 'github.com/LOSKIw',
                src: 'https://github.com/LOSKIw', 
                icon: require('@/images/icon/github.svg')
            },
            { 
                key: 'Bilibili', 
                label: '',
                src: 'https://space.bilibili.com/6156561', 
                icon: require('@/images/icon/Bilibili_Logo.svg')
            },
            { 
                key: 'steam', 
                label: 'steam:LOSKI',
                src: 'https://steamcommunity.com/id/CarOfLoski/', 
                icon: require('@/images/icon/steam.svg')
            },
        ],
        // 
        menus: [
            { label: ' ', value: '.anchor1' },
            { label: ' ', value: '.anchor2' },
            { label: ' ', value: '.anchor3' },
            { label: ' ', value: '.anchor4' },
        ],
        //  getAllPageOffsetTop 
        menusOffsetTop: [],
        // 
        selectedPageIndex: 0
    }),
    components: { PageTitle, NavigationBar, LinkItem },
    methods: {
        /**
         * 
         */
        getAllPageOffsetTop() {
            this.menusOffsetTop = this.menus.map(menu => {
                return this.getElementToPageTop(document.querySelector(menu.value))
            })
        },
        /**
         * 
         * 
         */
        onScroll() {
            const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop

            for (let index in this.menusOffsetTop) {
                index = Number(index)
                // 
                if (scrollTop >= this.menusOffsetTop[index]) {
                    const nextOffset = this.menusOffsetTop[index + 1]
                    // i
                    if (!nextOffset) {
                        this.selectedPageIndex = index
                        break
                    }
                    // 
                    if (scrollTop < nextOffset) {
                        this.selectedPageIndex = index
                        break
                    }
                }
            }
        }
    },
    mounted() {
        this.getAllPageOffsetTop()
        window.addEventListener('scroll', this.onScroll)
    },
    destroyed () {
        window.removeEventListener('scroll', this.onScroll)
    }
}
</script>
