<style lang="stylus" scoped>
.link-container
    cursor pointer
    display flex
    padding 6px 12px
    color #999999
    transition all .2s
    .src
        padding-left 16px
        line-height 32px
    
    &:hover
        background-color #e8e8e8
        border-radius 7px
</style>

<template lang="pug">
.link-container(@click="jumpTo")
    img.icon(:src="detail.icon")
    .src {{detail.label || detail.src}}
</template>

<script>
/**
 * 
 *  +  + 
 */
export default {
    name: 'LinkItem',
    props: {
        /**
         * 
         * 
         * @param {string} icon  require() 
         * @param {string} src 
         * @param {string} label 
         */
        detail: {
            type: Object,
            required: true
        }
    },
    data: () => ({ }),
    methods: {
        //  src 
        jumpTo() {
            window.open(this.detail.src)
        }
    }
}
</script>
